.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

button {
  margin: 5px;
  font-size: 1.2em;
  font-weight: bold;
  background: -webkit-gradient( linear, left bottom, left top, from(limegreen), to(lime));
  background: -webkit-linear-gradient( bottom, limegreen, lime);
  background: -o-linear-gradient( bottom, limegreen, lime);
  background: linear-gradient( to top, limegreen, lime); /* lawngreen, springgreen); */
  border-radius: 10px;
}
button:hover {
  -webkit-box-shadow: 0px 0px 10px yellow;
          box-shadow: 0px 0px 10px yellow;
}
button:focus {
  outline: 0;
  -webkit-box-shadow: 0px 0px 10px orange;
          box-shadow: 0px 0px 10px orange;
}

input {
  padding: 2px;
  line-height: 1.5em;
  border-radius: 5px;
}



